.aboutContainer {
display: flex;
justify-content: space-around;
align-items: center;

}

.mobileAbout {
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
}

.aboutChildren {
  text-align: center;
max-width: 500px;
height: auto;
animation: Scale 1s ease 0s 1 normal forwards;
}

.mobileChildren {
  text-align: center;
  animation: Scale 1s ease 0s 1 normal forwards;
  width: 100%;
  padding: 25px;
}

.aboutPhotos {
width: 500px;
height: auto;
}

.mobilePhotos {
width: 100%;
}

@keyframes Scale {
    0% {
      transform: scale(0);
    }
  
    100% {
      transform: scale(1);
    }
  }