body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  font-family: 'Lato', sans-serif;
  color: #2f2f2f;
  background-color: white;
 font-size: calc(1em + .5vw);
}

.header {
display: flex;
flex-direction: row;
justify-content: center;
align-items: flex-start;
  background-color: #1A2238;
  width: 100%;
}

.mobileHeader {
  background-color: #1A2238;
  width: 100%;
  position: relative;
display: flex;
flex-direction: column;
justify-content: flex-start;
height: 100%;
}

#hamburger {
position: absolute;
padding: 15px;
top: 0;
right: 0;
z-index: 300;
filter: brightness(0) saturate(0);
-webkit-filter: brightness(0) saturate(0);
}

#hamburgerLight {
  position: absolute;
  padding: 15px;
  top: 0;
  right: 0;
  z-index: 300;
}



.footer {
background-color: #1A2238;
color: white;
margin-top: 15px;
}