.navBar {
    display: flex;
  padding: 15px;
justify-content: center;
align-items: center;
    }
  
  .navMobile {
padding: 15px;
display: flex;
flex-direction: column;
justify-content: center;
align-items: flex-start;
height: 100vh;
animation: fadeIn 2s ease 0s 1 normal forwards;
  }
    
    .navLinks {
    padding: 12px;
    color: white;
    font-family: 'Lato', sans-serif;
    text-decoration: none;
    font-size: 20px;
    
    }

    .navLinks:hover {
    color: #FA8072;
    }
    @keyframes fadeIn {
      0% {
        opacity: 0;
      }
    
      100% {
        opacity: 1;
      }
    }